import React, { useEffect, useState } from 'react';
import { styled, createTheme, ThemeProvider } from '@mui/material/styles';
import CssBaseline from '@mui/material/CssBaseline';
import MuiDrawer from '@mui/material/Drawer';
import Box from '@mui/material/Box';
import MuiAppBar from '@mui/material/AppBar';
import Toolbar from '@mui/material/Toolbar';
import List from '@mui/material/List';
import Typography from '@mui/material/Typography';
import Divider from '@mui/material/Divider';
import IconButton from '@mui/material/IconButton';
import Container from '@mui/material/Container';
import Grid from '@mui/material/Grid';
import Paper from '@mui/material/Paper';
import ChevronLeftIcon from '@mui/icons-material/ChevronLeft';
import { format, parseISO } from 'date-fns';
import Table from '@mui/material/Table';
import TableBody from '@mui/material/TableBody';
import TableCell from '@mui/material/TableCell';
import TableHead from '@mui/material/TableHead';
import TableRow from '@mui/material/TableRow';
import TextField from '@mui/material/TextField';
import ListItemButton from '@mui/material/ListItemButton';
import ListItemIcon from '@mui/material/ListItemIcon';
import InputLabel from '@mui/material/InputLabel';
import MenuItem from '@mui/material/MenuItem';
import FormControl from '@mui/material/FormControl';
import Select from '@mui/material/Select';
import LowPriorityIcon from '@mui/icons-material/LowPriority';
import SearchIcon from '@mui/icons-material/Search';
import FilterAltIcon from '@mui/icons-material/FilterAlt';
import AnnouncementIcon from '@mui/icons-material/Announcement';
import CircleIcon from '@mui/icons-material/Circle';


function Copyright(props) {
  return (
    <Typography variant="body2" color="text.secondary" align="center" {...props}>
      {''} Listagem API | Teste de Desenvolvimento Front-end - Filipe Moura{'.'}
    </Typography>
  );
}

const drawerWidth = 240;

const AppBar = styled(MuiAppBar, {
  shouldForwardProp: (prop) => prop !== 'open',
})(({ theme, open }) => ({
  zIndex: theme.zIndex.drawer + 1,
  transition: theme.transitions.create(['width', 'margin'], {
    easing: theme.transitions.easing.sharp,
    duration: theme.transitions.duration.leavingScreen,
  }),
  ...(open && {
    marginLeft: drawerWidth,
    width: `calc(100% - ${drawerWidth}px)`,
    transition: theme.transitions.create(['width', 'margin'], {
      easing: theme.transitions.easing.sharp,
      duration: theme.transitions.duration.enteringScreen,
    }),
  }),
}));

const Drawer = styled(MuiDrawer, { shouldForwardProp: (prop) => prop !== 'open' })(
  ({ theme, open }) => ({
    '& .MuiDrawer-paper': {
      position: 'relative',
      whiteSpace: 'nowrap',
      width: drawerWidth,
      transition: theme.transitions.create('width', {
        easing: theme.transitions.easing.sharp,
        duration: theme.transitions.duration.enteringScreen,
      }),
      boxSizing: 'border-box',
      ...(!open && {
        overflowX: 'hidden',
        transition: theme.transitions.create('width', {
          easing: theme.transitions.easing.sharp,
          duration: theme.transitions.duration.leavingScreen,
        }),
        width: theme.spacing(7),
        [theme.breakpoints.up('sm')]: {
          width: theme.spacing(9),
        },
      }),
    },
  }),
);

const mdTheme = createTheme();

function Content() {
  var [ list, setList ] = useState( [] )
      
        useEffect( () => {
          fetch('http://137.184.244.33:3000/items')
          .then( async result => {
            setList( await result.json() )
          })
        }, [] )

  const [filter, setFilter] = useState('')
  const [filterPriority, setFilterPriority] = useState('')

  if(filter){
      const exp = eval(`/${filter.replace(/[^\d\w]+/,'.*')}/i`)
      list = list.filter( item => exp.test(item.number) || exp.test(item.type)) 
  }

  if(filterPriority){
      const exp2 = eval(`/${filterPriority.replace(/[^\d\w]+/,'.*')}/i`)
      list = list.filter( item => exp2.test(item.priority)) 
  }

  const handleFilter = e => {
      setFilter ( e.target.value )
  }

  const handleFilterPriority = e => {
      setFilterPriority ( e.target.value )
  }

  const [open, setOpen] = React.useState(true);
  const toggleDrawer = () => {
    setOpen(!open);
  };

  return (
    <ThemeProvider theme={mdTheme}>
      <Box sx={{ display: 'flex' }}>
        <CssBaseline />
        <AppBar position="absolute" open={open}>
          <Toolbar
            sx={{
              pr: '24px',
            }}
          >
            <IconButton
              edge="start"
              color="inherit"
              aria-label="open drawer"
              onClick={toggleDrawer}
              sx={{
                marginRight: '36px',
                ...(open && { display: 'none' }),
              }}
            >
              <FilterAltIcon />
            </IconButton>
            <Typography
              component="h1"
              variant="h6"
              color="inherit"
              noWrap
              sx={{ flexGrow: 1 }}
            >
              IPQ
            </Typography>
          </Toolbar>
        </AppBar>
        <Drawer variant="permanent" open={open}>
          <Toolbar
            sx={{
              display: 'flex',
              alignItems: 'center',
              justifyContent: 'flex-end',
              px: [1],
            }}
          >
            <IconButton onClick={toggleDrawer}>
              <ChevronLeftIcon />
            </IconButton>
          </Toolbar>
          <Divider />
          <List component="nav">
          <React.Fragment>
            <ListItemButton>
              <ListItemIcon onClick={toggleDrawer}>
                <SearchIcon />
              </ListItemIcon>
              <TextField sx={{minWidth: 150 }} id="" label="Buscar Nmero" onChange={handleFilter} /> 
            </ListItemButton>
            <ListItemButton>
              <ListItemIcon onClick={toggleDrawer}>
                <AnnouncementIcon />
              </ListItemIcon>
              <FormControl sx={{ mr: 3, minWidth: 150 }}>
              <InputLabel>Tipo </InputLabel>
              <Select label="Tipo" onChange={handleFilter}> 
                  <MenuItem value="">Todos</MenuItem>
                  {
                      list.map( ({type}) =>  {
                          return <MenuItem value={type}>{type}</MenuItem>
                      })
                  }
              </Select>
            </FormControl>
      
            </ListItemButton>
            <ListItemButton>
              <ListItemIcon>
                <LowPriorityIcon onClick={toggleDrawer}/>
              </ListItemIcon >
              <FormControl sx={{ mr: 3, minWidth: 150 }}>
                <InputLabel>Prioridade </InputLabel>
                <Select label="Prioridade" onChange={handleFilterPriority}> 
                    <MenuItem value="">Todas</MenuItem>
                    {
                        list.map( ({priority}) =>  {
                            return <MenuItem value={priority.toString()}>{priority}</MenuItem>
                        })
                    }
                </Select>
              </FormControl>
            </ListItemButton>
          </React.Fragment>
          </List>
        </Drawer>
        <Box
          component="main"
          sx={{
            backgroundColor: (theme) =>
              theme.palette.mode === 'light'
                ? theme.palette.grey[100]
                : theme.palette.grey[900],
            flexGrow: 1,
            height: '100vh',
            overflow: 'auto',
          }}
        >
          <Toolbar />
          <Container maxWidth="lg" sx={{ mt: 4, mb: 4 }}>
            <Grid container spacing={3}>
              <Grid item xs={12}>
                <Paper sx={{ p: 2, display: 'flex', flexDirection: 'column' }}>
                {list.map(( {number, type, subtype, date, priority, status_id, color }) => (<React.Fragment>
                  

                    <Typography component="p" variant="h4">
                    N<CircleIcon style={{ color: `${color}`, paddingTop:10, marginLeft:-25}} /> {number}
                    </Typography>
                    <Typography color="text.secondary" sx={{ flex: 1 }}>
                    {type} - {subtype}
                    </Typography>
                    <div>
                      
                      {format(parseISO(date), 'dd/MM/yyyy')} s {format(parseISO(date), 'hh:mm')}h. <br/>
                      Prioridade: {priority}<br/>
                      Status: {status_id}
                      
                    </div>
                    
                  </React.Fragment> ))}
                  
                  <React.Fragment>
                    <Table size="small">
                      <TableHead>
                        <TableRow>
                          <TableCell>Nmero</TableCell>
                          <TableCell>Tipo</TableCell>
                          <TableCell>Subtipo</TableCell>
                          <TableCell>Data</TableCell>
                          <TableCell align="right">Prioridade</TableCell>
                          <TableCell align="right">Status</TableCell>
                        </TableRow>
                      </TableHead>
                      <TableBody>
                        {list.map(( {number, type, subtype, date, priority, status_id, color }) => (
                          <TableRow  key={number}>
                            <TableCell><CircleIcon style={{ color: `${color}`, paddingTop:10, marginLeft:-25}} /> {number}</TableCell>
                            <TableCell>{type}</TableCell>
                            <TableCell>{subtype}</TableCell>
                            <TableCell>{format(parseISO(date), 'dd/MM/yyyy')} s {format(parseISO(date), 'hh:mm')}h</TableCell>
                            <TableCell align="right">{priority}</TableCell>
                            <TableCell align="right">{status_id}</TableCell>
                          </TableRow>
                        ))}
                      </TableBody>
                    </Table>
                  </React.Fragment>

                </Paper>
              </Grid>
            </Grid>
            <Copyright sx={{ pt: 4 }} />
          </Container>
        </Box>
      </Box>
    </ThemeProvider>
  );
}

export default function App() {
  return <Content />;
}